import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

import { AppComponent } from './app.component';
import { SinglePageComponent } from './components/app-single-page/app-single-page.component';
import { TopNavBarComponent } from './components/app-top-nav-bar/app-top-nav-bar.component';

@NgModule({
  declarations: [
    AppComponent,
    SinglePageComponent,
    TopNavBarComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }



